<template>
  <div>
    <div id="main" style="width: 1000px; height: 600px"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import "echarts-gl";
// https://datav.aliyun.com/portal/school/atlas/area_selector 从 DataV.GeoAtlas 获取广东省的 GeoJSON 数据
import geoJson from "./gd_geo.json";
echarts.registerMap("GD", geoJson);
export default {
  data() {
    return {
      echartsData: [
        { name: "广州市", value: 1882.7 },
        { name: "深圳市", value: 1779.01 },
        { name: "东莞市", value: 1048.53 },
        { name: "佛山市", value: 961.54 },
        { name: "湛江市", value: 707.84 },
        { name: "茂名市", value: 625.23 },
        { name: "惠州市", value: 607.34 },
        { name: "揭阳市", value: 565.36 },
        { name: "汕头市", value: 555.75 },
        { name: "江门市", value: 482.24 },
        { name: "中山市", value: 445.82 },
        { name: "肇庆市", value: 413.17 },
        { name: "清远市", value: 398.67 },
        { name: "梅州市", value: 384.91 },
        { name: "韶关市", value: 285.77 },
        { name: "河源市", value: 283.83 },
        { name: "汕尾市", value: 269.13 },
        { name: "阳江市", value: 262.47 },
        { name: "潮州市", value: 257.62 },
        { name: "珠海市", value: 249.41 },
        { name: "云浮市", value: 239.66 },
      ],
    };
  },
  methods: {
    initMap() {
      let myChart = echarts.init(document.getElementById("main"));

      let options = {
        title: {
          text: "ECharts 入门示例",
        },
        legend: {},
        tooltip: {
          trigger: "item",
          formatter: function (params) {
            return (
              params.seriesName +
              "<br/>" +
              params.name +
              ": " +
              params.value +
              "（xx）"
            );
          },
        },
        // 工具栏
        toolbox: {
          show: false,
          orient: "vertical",
          left: "right",
          top: "center",
          feature: {
            dataView: { readOnly: false },
            restore: {},
            saveAsImage: {},
          },
        },
        // 视觉映射组件
        visualMap: {
          show: false,
          inRange: {
            //定义 在选中范围中 的视觉元素
            color: ["#dfe0e7"],
          },
        },
        series: [
          {
            name: "广东2023年常住人口",
            type: "map3D",
            map: "GD",
            label: {
              show: true,
              textStyle: {
                color: "#192C7D",
                fontFamily: "Source Han Sans SC",
              },
            },

            itemStyle: {
              // 设置地图区域的多边形图形样式
              // areaColor: "#1b283b", // 区域填充颜色
              areaColor: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
                {
                  offset: 0,
                  color: "rgba(35, 205, 247, 1)", // 0% 处的颜色
                },
              ]),
              borderColor: "#fff", // 区域边框颜色
            },
            emphasis: {
              itemStyle: {
                // 鼠标悬浮时的样式
                areaColor: "rgba(96,172,252,0.5)", // 设置高亮时的填充颜色
                borderColor: "#60ACFC ", // 区域边框颜色
              },
              label: {
                show: true,
                textStyle: {
                  color: "#192C7D",
                },
              },
            },
            data: this.echartsData,
            click: function (params) {
              // params 是事件参数，包含了被点击区域的信息
              console.log("点击了某地级市:", params);
            },
          },
        ],
      };

      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(options);
    },
  },
  mounted() {
    this.initMap();
  },
};
</script>
